.current_swap_done_out{
  animation-name: swapDone1;
  animation-duration: 1.5s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(1,1,1,1);
}

@keyframes swapDone1{
  0%{
    transform:scale(1);
  }
  50%{
    transform:scale(1);
  }
  70%{
    transform:scale(0.8);
  }
  100%{
    transform:scale(0.8);
  }
}



.current_swap_done_item_out{
  animation-name: swapDone;
  animation-delay: 1.3s;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(1,1,1,1);
}

@keyframes swapDone{
  0%{
    transform:translateY(0px);
  }
  60%{
    transform:translateY(0px);
    opacity: 1;
  }
  80%{
    opacity: 0;
  }
  100%{
    transform:translateY(100px);
    opacity: 0;
  }
}

.current_swap_done_in{
  animation-name: swapDoneIn;
  /*animation-delay: 1.3s;*/
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(1,1,1,1);
  transform:scale(0.8);
}

@keyframes swapDoneIn{
  100%{
    transform:scale(1);
  }
  80%{
    transform:scale(0.8);
    opacity: 1;
  }
  20%{
    opacity: 0;
  }
  0%{
    transform:scale(0.8);
    opacity: 0;
  }
}




/*current_swap_done_item_out*/


.current_swap_done_item_in{
  animation-name: swapDoneItemIn;
  /*animation-delay: 1.3s;*/
  animation-duration: .5s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(1,1,1,1);
  transform:scale(0.8);
}

@keyframes swapDoneItemIn{
  100%{
    transform:translateY(0px);
  }
  80%{
    transform:translateY(0px);
    /*opacity: 1;*/
  }
  20%{
    /*opacity: 0;*/
  }
  0%{
    transform:translateY(-100px);
    /*opacity: 0;*/
  }
}


