}

@keyframes activeItem{
  0%{
    /*transform: scale(0)*/
    opacity: 0;
    transform: rotateX(-90deg);
  }
  40%{
    opacity: 1;
  }
  60%{
    transform: rotateX(30deg);
  }
  80%{
    transform: rotateX(-30deg);
  }
  100%{
    transform: rotateX(0deg);
  }
}

@keyframes deletedItem{
  0%{
    /*transform: scale(0)*/
    opacity: 0;
    transform: rotateX(90deg);
  }
  10%{
    opacity: 1;
  }
  100%{
    transform: rotateX(0deg);
  }
}

