.TwoFactorActivate{
  width: 100%;
  height: 100%;
  position: relative;
  display: grid;
  justify-items: center;
}

/*.TwoFactorActivate.success{
  opacity: 0;
}*/

.TwoFactorActivate.desaparecer{
  transform: translateY(30px);
}

.TLayout{
  width: 100%;
  height: 100%;
  max-width: 800px;
  position: absolute;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 25% 1fr;
  transition: .3s;
}

.layer1{
  z-index: 1;

}

.layer2{
  z-index: 2;
}

.header2fa{
  display: grid;
}

.header2fa h3{
  text-align: center;
  color: #00bfff;
  font-size: 25px;
}

.body2fa{
  height: 100%;
  /*background: white;*/
}

.bodySon{
  width: 100%;
  height: 50%;
  transition: .32s;
  display: grid;
  align-items: center;
  justify-items: center;
}

.footer2fa{
  width: 100%;
  min-height: 50%;
  height: auto;
  /*display: grid;*/
  position: relative;
}

.footer2fa .AuthInputComp{
  justify-self:center;
}

.footer2faText{
  position: relative;
  height: 100px;
}

.footer2faTextDes span{
  font-size: 22px !important;
}

.footer2faTextDes p{
  width: 80%;
  max-width: 650px;
  font-size: 16px;
}

.footer2faTextDes{
  position: absolute;
  overflow: hidden;
  transition: .3s;
  height: 0;
  width: 100%;
  display: grid;
  align-items: center;
  justify-items:center;
}

.footer2faTextDes.desp{
  animation-name: despAnim;
  animation-duration: .4s;
  /*animation-delay: 1s; */
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(1,1,1,1);
}

@keyframes despAnim{
  0%{
    height: 0;
  }
  60%{
    height: 0;
  }
  100%{
    height: 100px;
  }
}

.footer2faTextDes.pleg{
  animation-name: despAnim;
  animation-duration: 1s;
  animation-direction: reverse;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(1,1,1,1);
}



.header2fa .iconSty{
  justify-content: center !important;
}

.TwoFactorActivate p {
  margin: 0;
  color: gray;
  text-align: center;
}

.secretCode{
  color: #1babec !important;
  text-decoration: underline;
  height: 30px;
  font-size: 25px;
  margin-top: 10px !important;
}



.footer2fa #authReq{
  position: relative !important;
  margin-top: 20px;
}












