.VideoPlayer{
  width: 100%;
  max-width: 320px;
  height: 180px;
  /*overflow: hidden;*/
  position: fixed;
  /* bottom: 60px; */
  top: 80px;
  right: 20px;
  z-index: 99999;
}

.videoPlayerContainer{
  width: 100%;
  height: 100%;
  background: black;
  display: grid;
  position: relative;
}

.handleAction{
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 2;
}


.closeButtonPlayer{
  position: absolute;
  top: -10px;
  right: 5px;
  border-radius: 50%;
  background: white;
  width: 25px;
  height: 25px;
  display: grid;
  align-items: center;
  justify-items:center;
  cursor: pointer;
  transition: .2s;
}

.closeButtonPlayer:hover{
  transform: scale(1.12);
}

.closeButtonPlayer i{
    font-size: 25px;
    color: black !important;
}

.ytp-chrome-top{
  display: none !important;
  opacity: 0 !important;
 }
