.enableMap{
  /*opacity: 0;*/
  animation-name: enableMap;
  animation-duration: 1s;
  /*animation-delay: .4s;*/
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(1,1,1,1);
}

@keyframes enableMap{
  0%{
    opacity: 0;
    transform: scale(0);
  }
  50%{
    opacity: 1;
    transform: scale(0);
  }
  80%{
    transform: scale(1.2);
  }
  100%{
    transform: scale(1);
  }
}
