.loaderLayout.in{
  animation-name: authSwitchAnim;
  animation-duration: .15s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(1,1,1,1);
}


@keyframes authSwitchAnim{
  0%{
    opacity: 0;
    transform: translateY(5px);
  }
  100%{
    opacity: 1;
    transform: translateY(0px);
  }
}

.loaderLayout.out{
  animation-name: authSwitchAnimOut;
  animation-duration: .15s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(1,1,1,1);
}


@keyframes authSwitchAnimOut{
  100%{
    opacity: 0;
    transform: translateY(-5px);
  }
  0%{
    opacity: 1;
    transform: translateY(0px);
  }
}





